<template>
  <div class="chart-container">
    <canvas id="myChart"></canvas>
  </div>
</template>
<script>
import Store from '../store';
import Chart from 'chart.js';
import 'chartjs-plugin-streaming';

export default {
  data() {
    return {
      historical: false,
      last: null,
      lastSourceId: null,
    };
  },
  computed: {
    sourceId() {
      return Store.getters.sourceId;
    },
  },
  mounted() {
    let ctx = document.getElementById('myChart').getContext('2d');
    let that = this;
    new Chart(ctx, {
      type: 'line', // 'line', 'bar', 'bubble' and 'scatter' types are supported
      data: {
        datasets: [
          {
            data: [], // empty at the beginning
          },
        ],
      },
      options: {
        maintainAspectRatio: false,
        legend: false,
        scales: {
          xAxes: [
            {
              type: 'realtime', // x axis will auto-scroll from right to left
              time: {
                displayFormats: {
                  second: 'HH:mm:ss',
                },
              },
              realtime: {
                // per-axis options
                duration: 1000 * 60, // data in the past 20000 ms will be displayed
                refresh: 1000, // onRefresh callback will be called every 1000 ms
                // delay: 1000, // delay of 1000 ms, so upcoming values are known before plotting a line
                pause: false, // chart is not paused
                ttl: undefined, // data will be automatically deleted as it disappears off the chart
                // a callback to update datasets
                onRefresh: function(chart) {
                  if (
                    that.lastSourceId === null ||
                    that.sourceId() !== that.lastSourceId
                  ) {
                    that.isChanged = false;
                    chart.data.datasets[0].data = Store.getters.prices.map(
                      p => {
                        return {
                          x: p.time,
                          y: p.price,
                        };
                      }
                    );
                    return;
                  }
                  // query your data source and get the array of {x: timestamp, y: value} objects
                  let lastPrice = Store.getters.lastPrice;
                  if (lastPrice && that.last !== lastPrice) {
                    let data = {
                      x: lastPrice.time,
                      y: lastPrice.price,
                    };
                    chart.data.datasets[0].data.push(data);
                    that.last = lastPrice;
                  }
                  // append the new data array to the existing chart data
                  // Array.prototype.push.apply(myChart.data.datasets[0].data, data);
                },
              },
            },
          ],
        },
        plugins: {
          streaming: {
            // per-chart option
            frameRate: 60, // chart is drawn 30 times every second
          },
        },
      },
    });
  },
  props: { chartData: Array },
};
</script>

<style scoped>
.chart-container {
  height: 50vh;
}
</style>
